@import url("https://fonts.googleapis.com/css?family=Poppins&family=Poppins&family=Langar&display=swap");

/* Header */
.panel-out {
  margin: 0px;
  padding: 0px;
}

.text-color-red {
  color: rgb(255, 72, 40);
}
.text-color-green {
  color: rgb(42, 185, 147);
}
.text-color-black {
  color: rgb(0, 0, 0);
}
.text-color-blue {
  color: rgb(23, 27, 224);
}

.background-color-red {
  background-color: rgb(255, 72, 40);
}
.background-color-green {
  background-color: rgb(42, 185, 147);
}
.background-color-black {
  background-color: rgb(0, 0, 0);
}
.background-color-blue {
  background-color: rgb(23, 27, 224);
}

.panel {
  width: 1190px;
  height: 1239px;
  margin: 0 auto;
  background-color: #fff;
}

.pad-75 {
  width: 100%;
  padding-left: 70px;
  padding-right: 70px;
  max-height: 200px;
  align-items: center;
  padding-top: 10px;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.profile-pic {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}

.header-text {
  padding-left: 40px;
}

.name-styles {
  font-family: Poppins;
  font-size: 60px;
  font-weight: 700;
  margin-bottom: -15px;
}

.sub-name-styles {
  font-family: Poppins;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
}

/* Body */
.left-side-body {
  width: 25%;
}

.heading-text {
  line-height: 1;
  margin-top: 25px;
  padding: 5px;
  color: #000;
  font-weight: 700;
  text-transform: uppercase;
  font-family: Poppins;
  font-size: 15.5px;
}

.sub-heading-text {
  line-height: 1;
  margin-top: 5px;
  padding: 5px;
  font-weight: 700;
  font-family: Poppins;
  font-size: 15.5px;
}

.clear-padding {
  padding: 0px;
}

p {
  padding: 0px;
  margin: 0px;
  margin-block-start: 0px;
  margin-block-end: 0px;
  font-family: Poppins;
}

.padding-small {
  padding: 5px;
}
.padding-medium {
  padding: 10px;
}

.margin-small {
  margin: 5px;
}

.margin-top-small {
  padding-top: 5px;
}

.profile-text {
  line-height: 1.4;
  font-size: 14px;
  margin-top: -5px;
}

p strong {
  font-weight: bold;
}

.icon-size {
  width: 20px;
  height: 20px;
}

.border-bottom-solid-black {
  border-bottom: 1px solid #000;
}

.align-self-center {
  align-self: center;
}

.max-height-20 {
  max-height: 20px;
}

.icons-text-style {
  font-size: 14px;
  font-family: Poppins;
}

.dot-field {
  width: 10px;
  height: 10px;
  /* background-color: rgb(42, 185, 147); */
  border-radius: 50%;
  align-self: center;
}

.margin-top-clear {
  margin-top: 0px;
}

.space-btw {
  justify-content: space-between;
}

.padding-left-20 {
  padding-left: 20px;
}

.font-family-poppins {
  font-family: Poppins;
}

.text-bold {
  font-weight: 700;
}

.vertical-line-css {
  position: absolute;
  height: 5%;
  width: 4.8px;
  background: #000;
  opacity: 0.2;
  margin-left: 8px;
}
.horizontal-line-css {
  display: inline-block;
  border-left: 0;
  width: 38px;
  height: 6.14px;
  float: left;
  cursor: pointer !important;
  margin: 15px 0px;
  border: 2px solid #fff;
}

.padding-bottom-clear {
  padding-bottom: 0px;
}

.padding-top-small {
  padding-top: 5px;
}

.font-size-13 {
  font-size: 13px;
}

.font-size-14 {
  font-size: 14px;
}

.padding-verti-15 {
  padding-top: 15px;
  padding-bottom: 15px;
}

.technology_name {
  align-self: center;
  padding-left: 15px;
}

.color_base {
  opacity: 0.25;
}

.min-width-320 {
  min-width: 320px;
}

.padding-left-15 {
  padding-left: 15px;
}

.padding-top-20 {
  padding-top: 20px;
}
.padding-bottom-20 {
  padding-bottom: 20px;
}

.circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #0d0c0c;
}

.flex-row-reverse {
  display: flex;
  flex-direction: row-reverse;
}

.cursor-pointer {
  cursor: pointer;
}
